import React, {Component} from 'react';
import ListItems from '../../repeatElements/listItems';
import AboutItems from '../../repeatElements/aboutItems';
import Service from '../../../service';
import Error from '../../repeatElements/error';

import Filter from './filter';

import './coffeePage.sass';

export default class CoffeePage extends Component {

	state = {
		coffeeList: [],
		filteredText: [],
		filteredCountry: [],
		visiblePosts: [],
		error: false,
		errorNumber: null,
		activeBrazil: '',
		activeKenya: '',
		activeColumbia: '',
		activeAll: 'active-filter-btn'

	}
	componentDidMount = () => this.getInfo();
	
	componentDidCatch = () => {this.setState({error: true})}

	onTextFilter = (e) => {
		const value = e.target.value;
		const {coffeeList} = this.state;

		let filteredArray = coffeeList.filter( item => {
			const isFilter = item.name.toLowerCase().indexOf(value.toLowerCase()) > -1;
			if (isFilter) return item;
			else return null
		})
		this.setState({filteredText: filteredArray})
	}

	arrayFilterCountry = (filter, isButton = false) => {
		const {coffeeList} = this.state;
		const filteredArray = coffeeList.filter( item => {
			if(item.country === filter) return item;
			else return null;
		})
		if (filter === 'all') this.setState({filteredCountry: coffeeList})
		else if (isButton) this.setState({filteredCountry: filteredArray})
	}

	setActiveBtn(filter){
		if(filter === 'Brazil') this.setState({activeBrazil: 'active-filter-btn', activeKenya: '', activeColumbia: '', activeAll: '' });
		if(filter === 'Kenya') this.setState({activeBrazil: '', activeKenya: 'active-filter-btn', activeColumbia: '', activeAll: ''});
		if(filter === 'Columbia') this.setState({activeBrazil: '', activeKenya: '', activeColumbia: 'active-filter-btn', activeAll: ''});
		if(filter === 'all') this.setState({activeBrazil: '', activeKenya: '', activeColumbia: '', activeAll: 'active-filter-btn'});
	}

	onCountryFilter = (filter) => {
		this.setActiveBtn(filter);
		this.arrayFilterCountry(filter, true);
	} 

	filterPost = (filteredText, filteredCountry) => {
		let arr = [];
		filteredText.forEach( itemText => {
			filteredCountry.forEach( itemCountry => {
				if (itemText.name === itemCountry.name) arr.push(itemText)
			})
		})
		return arr;
	}

	getInfo = () => {
		const {coffeeList} = this.state;
		let country = null;

		new Service()
			.getPosts()
			.then( res => {
				if(coffeeList.length === 0) {
					this.setState({
						coffeeList: res.coffee,
						filteredText: res.coffee,
						filteredCountry: res.coffee,
					})
				}

			})
			.catch( err => this.setState({error: true, errorNumber: Number.parseInt(err)}))		
		return country;
	}

	render() {
		const {filteredText, filteredCountry, error, errorNumber, activeBrazil, activeKenya, activeColumbia, activeAll} = this.state;
		const visiblePosts = this.filterPost(filteredText, filteredCountry);
		this.getInfo();
		return (
			<section  className="shop">
				<div  className="container">
					<AboutItems
						title='About our goods'
						img='img/coffee_girl.jpg'
					/>

					<Filter
						onTextFilter={this.onTextFilter}
						onCountryFilter={this.onCountryFilter}
						activeBrazil={activeBrazil}
						activeKenya={activeKenya}
						activeColumbia={activeColumbia}
						activeAll={activeAll}
					/>
					{
						error ?	<Error errorNumber={errorNumber}/>
						: <ListItems>
								{{
									id: 'coffeePage',
									elements: visiblePosts
								}}
							</ListItems>
					}	 
					
				</div>
			</section>
		)
	}
}