import React, {Component} from 'react';
import {withRouter } from 'react-router-dom';

import Service from '../../../service';
import Spinner from '../spinner';
import Error from '../error';

import './listItems.sass';

export class ListItems extends Component {

	state = {
		itemList: [],
		loading: true,
		error: false,
		errorNumber: null,
		coffeeNotFound: 0
	}

	componentDidCatch(){
		this.setState({error: true})
	}

	onItemPage = (id, item) => {
		item['id'] = id;
		if(id === 'mainPage') {
			new Service()
			.getPosts()
			.then( res => {
				res.coffee.forEach( elem => {
					if(item.name === elem.name) {
						this.props.history.push(`/coffee/${item.name.replace(/ /g,"_")}`, elem);
					}
				})	
			})
			
		}
		else if(id === 'pleasurePage') {
			this.props.history.push('/pleasure', item);
		}
		else if(id === 'coffeePage') {
			this.props.history.push(`/coffee/${item.name.replace(/ /g,"_")}`, item);
			
		}
	}

	getData = (array, id) => {
		return array.map( (item, i) => {
			const {url, name, price, country = null} = item;
			const key = `keyOurBestList ${i}` + (new Date().getTime() - Math.floor(Math.random() * 1539199440000));
				return(
					<Item
						isCountry={country}
						key={key}
						url={url}
						name={name}
						country={country}
						price={price}
						onClick={() => this.onItemPage(id, item)}
					/>
				)
			
		})
	}
	
	setValueAndLoading(value) {
		const {coffeeNotFound} = this.state;
		if(value.length !== 0) {
			this.setState({itemList: value, coffeeNotFound: 1});
			setTimeout(() => {
				this.setState({loading: false})
			}, 300);
		}
		else if (value.length === 0){
			if (coffeeNotFound === 1) this.setState({coffeeNotFound: 2});
			if (coffeeNotFound === 0) this.setState({coffeeNotFound: 1});
		}
		
	}

	renderItems = () => {
		const {itemList, coffeeNotFound} = this.state;
		const child = this.props.children;
		const coffee = child.elements;
			new Service()
				.getPosts()
				.then( res =>{
					
					let value = [];
					if(child.id === 'mainPage') value = this.getData(res.bestsellers, child.id);
					else if(child.id === 'pleasurePage') value = this.getData(res.goods, child.id);
					else if(child.id === 'coffeePage') value = this.getData(coffee, child.id);

					if (itemList.length === 0) return this.setValueAndLoading(value);
					if(value.length !== itemList.length	|| itemList[0].props.name !== value[0].props.name) {
						this.setValueAndLoading(value);
					}
					if (coffeeNotFound === 2) this.setValueAndLoading(value);
				})
				.catch( err => this.setState({error: true, errorNumber: Number.parseInt(err)}))
	}

	render() {
		const {itemList, loading, error, errorNumber, coffeeNotFound} = this.state;
		this.renderItems();

		let content = null;
		if (error) content = <Error errorNumber={errorNumber}/>
		else if (coffeeNotFound === 2) content = <Error errorNumber={100500}/>
		else if (loading) content = <Spinner/>
		else content = itemList;
		return (
			<div  className="row">
				<div  className="col-lg-12">
					<div  className="shop__wrapper">
						{content}
					</div>
				</div>
			</div>
		)
	}
}
export default  withRouter(ListItems);

function Item({url, name, isCountry, country, price, onClick,}) {
	return (
		<div 
			className="shop__item"
			onClick={onClick}
		>
			<img src={url} alt=""/>
			<div  className="shop__item-title">
				{name}
			</div>
			{isCountry ? <div  className="shop__item-country">{country}</div> : null}
			<div  className="shop__item-price">{price}</div>
		</div>
	)
}
