import React from 'react';

export default ({onTextFilter, onCountryFilter, activeBrazil, activeKenya, activeColumbia, activeAll}) => {

	let isBrazil = false 
	if (activeBrazil !== '') isBrazil = true
	else isBrazil = false;
	
	let isKenya = false 
	if (activeKenya !== '') isKenya = true
	else isKenya = false;
	
	let isColumbia = false 
	if (activeColumbia !== '') isColumbia = true
	else isColumbia = false;

	let isAll = false 
	if (activeAll !== '') isAll = true
	else isAll = false;

	return (
		<>
			<div className="shop__filter-wrapper">
					<form action="#"  className="shop__search">
							<label  className="shop__search-label" htmlFor="filter">Looking for</label>
							<input
								id="filter"
								type="text"
								placeholder="start typing here..." 
								className="shop__search-input"
								onChange={onTextFilter}
							/>
					</form>
					<div  className="shop__filter">
							<div  className="shop__filter-label">
								Filter
							</div>
							<div  className="shop__filter-group">
							<button
								disabled={isAll}
								className={`shop__filter-btn reset-all-filter ${activeAll}`}
								onClick={ () => onCountryFilter('all')}	
							>x</button>
							<button
								disabled={isBrazil}
								className={`shop__filter-btn ${activeBrazil}`}
								onClick={ () => onCountryFilter('Brazil')}	
							>Brazil</button>
							<button
								disabled={isKenya}
								className={`shop__filter-btn ${activeKenya}`} 
								onClick={ () => onCountryFilter('Kenya')}	
							>Kenya</button>
							<button  
								disabled={isColumbia}
								className={`shop__filter-btn ${activeColumbia}`} 
								onClick={ () => onCountryFilter('Columbia')}	
							>Columbia</button>
							</div>
						</div>
			</div>
		</>
	)
}