import React, {Component} from 'react';
import ListItems from '../../repeatElements/listItems';
import AboutItems from '../../repeatElements/aboutItems';

export default class PleasurePage extends Component {

	render() {
		let about = null;
		
		if(this.props.location.state === undefined) {
			about = 	(
				<AboutItems
					title='About our beans'
					img='img/coffee_good.jpg'
				/>
			)
		} else {
			const {
				name = 'About our beans',
				url = 'img/coffee_good.jpg',
			}	= this.props.location.state;;
			about = (
				<AboutItems
					title={name}
					img={url}
				/>
			)
		}
		
		return (
			<section  className="shop">
				<div  className="container">
					{about}
					<ListItems>
					{{
						id: 'pleasurePage',
					}}
					</ListItems>
				</div>
			</section>
		)
	}
}