import React, {Component} from 'react';
import {withRouter, Link} from 'react-router-dom';
import MenuList from '../repeatElements/menuList'
import './header.sass';


class Header extends Component {

	render() {
		const {pathname} = this.props.history.location;
		let element = null;
		let title = null;

		if (pathname === '/') {
			element = (
				<div className="preview">
					<div className="container">
						<MenuList
							ulClassName="header"
							liClassName="header__item"
							logo="logo/Logo.svg"
						/>
						<div className="row">
							<div className="col-lg-10 offset-lg-1">
								<h1 className="title-big">Everything You Love About Coffee</h1>
								<img className="beanslogo" src="logo/Beans_logo.svg" alt=""/>
								<div className="preview__subtitle">We makes every day full of energy and taste</div>
								<div className="preview__subtitle">Want to try our beans?</div>
								<Link to='/coffee'>
									<span className="preview__btn">More</span>
								</Link>
							</div>
						</div>
					</div>
				</div>
			)
		}
		else {
			let classBg = 'banner';
			const coffeePath = pathname.match('^/coffee');
			
			if (pathname === '/contact') {
				title = 'Contact us';
				classBg += '-contact';
			} 
			else if (pathname === '/pleasure') {
				title = 'For your pleasure';
				classBg +='-pleasure';
				
			} 
			else if (pathname === '/coffee' || pathname === '/coffee/') {
				title = 'Our Coffee';
				classBg +='-coffee';
				
			}
			else if (coffeePath !== null && coffeePath[0] === '/coffee' & this.props.location.state !== undefined) {
				title = this.props.location.state.name;
				classBg +='-coffee';
			}
			else {
				title = 'Page Not Found';
				classBg +='-coffee';
			}
			element = (
				<div className={classBg}>
					<div className="container">
						<MenuList
							ulClassName="header"
							liClassName="header__item"
							logo="logo/Logo.svg"
						/>
						<h1 className="title-big">{title}</h1>
					</div>
				</div>
			)
		}
		return element
	}
}
export default withRouter(Header)

	

	
