
import React from 'react';
import './error.sass';

export default ({errorNumber})  => {
	
	let src = null,
		text = null;
	
	if(errorNumber === 404){
		src = '/img/error404.gif';
		text = 'Data not found';
	}
	if(errorNumber === 100500){
		src = '/img/error404.gif';
		text = 'Coffee not found';
	} else {
		src = '/img/error.gif';
		text = 'Something goes wrong';
		errorNumber = 'Critical';
	} 

	return(
		<div className="error__wrapper">
			<img className="error__img"src={src} alt="error" />
			<div className="error__text"> {text} </div>
			{ errorNumber === 100500 ? null : <div className="error__text"> Error: {errorNumber} </div> }
		</div>
	) 
}