import React, {Component} from 'react';
import Error404Page from '../error404Page'

import './itemPage.sass';
export default class ItemPage extends Component {

	constructor(props) {
		super(props)
		if(this.props.location.state !== undefined){
			const description = props.location.state.description;
			this.state = {
				description: description.substring(0, description.length - (description.length - 200)),
				readMoreClass: 'shop__read-more',
				textClass: 'shop__item-text',
				error: false
			}
		} else {
			this.state = {
				error: true
			}
		}
		
	}

	
	onReadMore = () => {
		this.setState({
			description: this.props.location.state.description,
			readMoreClass: 'shop__read-more disabled',
			textClass: 'shop__item-full-text'
		})
	}

	onBack = () => {
		this.props.history.goBack();
	}

	render() {
		if (this.state.error) return <Error404Page/>
		const {country, price, url} = this.props.location.state;
		const {description, readMoreClass, textClass} = this.state;
		return (
			<>
				<section className="shop">
					<div className="container">
						<div className="row">
							<div className="col-lg-5 offset-lg-1">
								<img className="shop__item-img" src={url} alt=""/>
							</div>
							<div className="col-lg-4">
								<div className="title">About it</div>
								<img className="beanslogo" src="logo/Beans_logo_dark.svg" alt=""/>
								<div className="shop__point">
									<span>Country: </span>
										{country}
								</div>
								<div className="shop__point">
									<span>Description: </span>
									<span 
										onClick={this.onReadMore}
										className={textClass}
										>{description} </span>
									<span
										className={readMoreClass}
										onClick={this.onReadMore}
									>...</span>
								</div>
								<div className="shop__point">
									<span>Price: </span>
									<span className="shop__point-price">{price}</span>
								</div>
							</div>
						</div>
						<button
							className="shop__button-back"
							onClick={this.onBack}
						>
							Come back
						</button>
					</div>
				</section>
			</>
		)
	}
}