import React, {Component} from 'react';
import Thanks from './thanks';
import Contacts from './contacts';
import Error from '../../repeatElements/error';
import Service from '../../../service';
import Spinner from '../../repeatElements/spinner';

export default class ContactPage extends Component {

	state = {
		name: '',
		email: '',
		phone: '',
		message: '',
		className: '',
		classEmail: '',
		classMessage: '',
		isName: false,
		isEmail: false,
		isMessage: false,
		phoneFocus: false,
		send: false,
		error: false,
		loading: false
	}

	componentDidCatch(){
		this.setState({error: true})
	}

	onName = (e) => {
		const value = e.target.value;
		this.setState({name: value})
		if(value.length >= 2) this.setState({isName: true, className: ''})
		else {this.setState({isName: false})}
	} 

	onMessage = (e) => {
		const value =  e.target.value;
		this.setState({message: value})
		if(value.length > 0) this.setState({isMessage: true, classMessage: ''})
		else {this.setState({isMessage: false})}
	}

	onEmail = (e) => {
		const value =  e.target.value;
		const maskEmail = /^(([^<>()\\.,;:\s@"]+(\.[^<>()\]\\.,;:\s@"]+)*)|(".+"))@(([0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		const isValue = maskEmail.test(value);
		if(isValue) this.setState({classEmail: ''})
		this.setState({
			email: value,
			isEmail: isValue
		})
	}

	onPhoneFocus = (e) => {
		if(e.target.value.length < 3) e.target.value = '+7 (';
		this.setState({phoneFocus: true})
	}

	onPhone = (e) => {
		const lastChar = Number.parseInt(e.target.value.toString().slice(-1)); 
		if (e.target.value.length === 17) return; 
		if (lastChar >= 0 && lastChar <= 9 && e.target.value.length <= 18) {
			this.setState({phone: e.target.value})
		}
		if(e.target.value.length > 17) {
			e.target.value = e.target.value.substring(0, e.target.value.length - 1);
		} 
			e.target.value = e.target.value.replace('+7', "");
			e.target.value = e.target.value.replace(/\D/g, "");
			e.target.value = e.target.value.replace(/^(\d{3})(\d)/g, "$1) $2");
			e.target.value = '+7 (' + e.target.value.replace(/(\d{3})(\d)/, "$1-$2");

	}

	onSend = () => {
		const {isName, isEmail, isMessage} = this.state
		if(!isName) this.setState({className: 'error'})
		if(!isEmail) this.setState({classEmail: 'error'})
		if(!isMessage) this.setState({classMessage: 'error'})

			if (isName && isEmail && isMessage) {
				this.postContacts();	
			}
	}
	onBack = () => {
		this.setState({
			name: '',
			email: '',
			phone: '',
			message: '',
			className: '',
			classEmail: '',
			classMessage: '',
			isName: false,
			isEmail: false,
			isMessage: false,
			phoneFocus: false,
			send: false,
			error: false,
			loading: false
		})	
	}

	postContacts() {
		const {name, email, phone, message, isName, isEmail, isMessage} = this.state
		this.setState({loading: true})

		if (isName && isEmail && isMessage) {
			// new Service().postResource('http://localhost:3004/contacts', {name, email, phone, message})
			new Service().postResource('https://jsonplaceholder.typicode.com/posts', {name, email, phone, message})
			.then ( () => this.setState({send: true, loading: false}))
			.catch ( () => this.setState({error: true}))
		}
	}
	render() {
		const {error, send, className, classEmail, classMessage, loading} = this.state;

		let content = null;
		if (error) content = (
			<>
				<Error/>
				<button
					className="shop__button-back"
					onClick={this.onBack}
				>Back to form</button>
			</>
		)						
		else if (loading) content = <Spinner/>
		else if (send) content = <Thanks	onBack={this.onBack}/>
		else content = (
			<Contacts
				onName={this.onName}
				onEmail={this.onEmail}
				onPhone={this.onPhone}
				onMessage={this.onMessage}
				className={className}
				classEmail={classEmail}
				classMessage={classMessage}
				onSend={this.onSend}
				onPhoneFocus={this.onPhoneFocus}
			/> 
		)
		return (
			<>
				<h2 className="contact__title title">Tell us about your tastes</h2>
				<img className="beanslogo" src="logo/Beans_logo_dark.svg" alt=""/>
				{content}
			</>
		)
	}
}
