import React, {Component} from 'react';
import {Link, withRouter} from 'react-router-dom';
import './menuList.sass';

class MenuList extends Component {

	render() {
		const {pathname} = this.props.history.location
		const {ulClassName, liClassName, logo} = this.props;
		let coffeeLink, pleasureLink, contactLink = null;
		if(pathname === '/coffee') coffeeLink = 'menu-list--active'
		else if(pathname === '/pleasure') pleasureLink = 'menu-list--active'
		else if(pathname === '/contact') contactLink = 'menu-list--active'
	
		return (
			<ul className={ulClassName}>
				<li className={liClassName}>
				<Link to='/'>
					<span>
						<img src={logo} alt=""/>
					</span>
				</Link>
					
				</li>
				<li className={liClassName}>
					<Link to='/coffee'>		
						<span className={coffeeLink}>Our coffee</span>
					</Link>		
				</li>
				<li className={liClassName}>
					<Link to='/pleasure'>
						<span className={pleasureLink}>For your pleasure</span>
					</Link>	
				</li>
				<li className={liClassName}>
					<Link to='/contact'>
						<span className={contactLink}>Contact us</span>
					</Link>
				</li>
			</ul>
		)
	}
}
export default withRouter(MenuList);